import React from 'react'
import './../css/Group.css'
import axios from 'axios'
import Positions from './Positions'
import Entry from './Entry'
import Container from 'react-bootstrap/Container'
import 'bootstrap/dist/css/bootstrap.min.css'
import Match from './Match'
import Button from 'react-bootstrap/Button'

const Group = () => {
    const [playersList, setPlayersList] = React.useState([]);
    const [player, setPlayer] = React.useState({name: '', team: '', matches_played: 0, 
                                               points: 0, wins: 0, draws: 0, losses: 0});
    const [match, setMatch] = React.useState({teams: ['',''], score: [0, 0]});

    axios.defaults.xsrfHeaderName = "X-CSRFTOKEN";
    axios.defaults.xsrfCookieName = "csrftoken";

    function componentDidMount() {
        refreshList();
    };

    function refreshList() {
        axios
        .get("/api/players/")
        .then((res) => {
            let newList = [...res.data];
            newList.sort((teamA, teamB) => teamB['points'] - teamA['points']);
            setPlayersList(newList)})
        .catch((err) => console.log(err));
    };

    function handleSubmit(item) {
        axios.defaults.withCredentials = true;
        axios.post('admin/login/', { username: 'juanmd', password: 'dadada77'})
             .then(res => updatePlayers(item))
             .catch(err => console.log('Login error', err.response))
             .finally(refreshList)
    };

    function updatePlayers(item) {
        axios.post('/api/players/', item)
        .then(res => alert('Equipo agregado'))
        .catch(err => console.log(err))
    }
    function handleChange(event){
        event.preventDefault();
        const field = event.target.name;
        const value = event.target.value;
        setPlayer({...player, [field]: value});
    };

    function handleAdd(event) {
        event.preventDefault();
        handleSubmit(player);
        setPlayer({name: '', team: '', matches_played: 0, 
        points: 0, wins: 0, draws: 0, losses: 0});
    };

    function handleNewMatch(event){
        event.preventDefault();
        const field = event.target.name;
        const type = event.target.id === 'local' ? 0 : 1;
        const value = event.target.value;
        let fieldArray = match[field];
        fieldArray[type] = value;
        setMatch({...match, [field]: fieldArray});
    };

    function handleDelete(item) {
        axios
          .delete(`/api/players/${item.id}/`)
          .then((res) => refreshList());
      };

    function handleChangePositions(event){
        event.preventDefault();
        if (match['score'][0] === match['score'][1]){
            for (let someTeam of playersList){
                if(someTeam['team'] === match['teams'][0] || someTeam['team'] === match['teams'][1]){
                    someTeam['matches_played'] += 1;
                    someTeam['draws'] += 1;
                    someTeam['points'] += 1;
                }
                axios
                    .put(`/api/players/${someTeam.id}/`, someTeam)
                    .then((res) => refreshList());
            }
        }else{
            for (let someTeam of playersList){
                const winner = match['score'][0] > match['score'][1] ? match['teams'][0] : match['teams'][1];
                if(someTeam['team'] === match['teams'][0] || someTeam['team'] === match['teams'][1]){
                    someTeam['matches_played'] += 1;
                    if(someTeam['team'] === winner){
                        someTeam['wins'] += 1;
                        someTeam['points'] += 3;
                    }else{
                        someTeam['losses'] += 1;
                    }
                    axios
                        .put(`/api/players/${someTeam.id}/`, someTeam)
                        .then((res) => refreshList());
                }
            }
        }
    };

    return (
        <Container>
            <img src = "https://img.fifa.com/image/upload/t_tc1/oqp6macvzvwwfbb0kuhs.jpg" />
            <div className = "group-container">
                <h1 style = {styles_title}>Laderas Cup</h1>
                <div className = "group">
                    <Button type="submit" variant = "danger" className="mb-2" onClick={refreshList}>
                        Refresh
                    </Button>
                    <Button type="submit" variant = "danger" className="mb-2" onClick={(event) => {
                        event.preventDefault();
                        for (const toRemoveTeam of playersList){
                            handleDelete(toRemoveTeam);
                        }
                    } }>
                        Delete all
                    </Button>
                    <Entry onAdd = {handleAdd} onChange = {handleChange} />
                    <Match onAdd = {handleChangePositions} onChange = {handleNewMatch}/>
                    <Positions list = {playersList} />
                </div>
            </div>
        </Container>
    );
};

  
const styles_title = {
    color: "white",
    fontSize: "70px",
    fontFamily: 'Roboto Slab',
};


export default Group
