import React from 'react'
import Table from 'react-bootstrap/Table'

const Positions = ({list}) => {
    return (
        <Table striped hover variant = "light">
            <thead>
                <tr>
                    <th>Flag</th>
                    <th>Position</th>
                    <th>Player</th>
                    <th>Team</th>
                    <th>Matches</th>
                    <th>Points</th>
                    <th>Wins</th>
                    <th>Draws</th>
                    <th>Losses</th>
                </tr>
            </thead>
            <tbody>
                {list.map((player, position) => playerView(player, position))}
            </tbody>
         </Table>
    );
}

const playerView = (player, position) => {
    const flag = `https://img.icons8.com/color/48/000000/${player.team}.png`;
    const teamName = (player.team).charAt(0).toUpperCase() + (player.team).slice(1);
    return (
        <tr style = {{fontSize: "20px"}}> 
            <td><img src={flag} style ={styles_img}/></td>
            <td>{position+1}</td>
            <td>{player.name}</td>
            <td>{teamName}</td>
            <td>{player.matches_played}</td>
            <td>{player.points}</td>
            <td>{player.wins}</td>
            <td>{player.draws}</td>
            <td>{player.losses}</td>
        </tr>

    );
};

const styles_img = {
    minHeight: "100%",
    minWidth: "48px",
      
    /* Set up proportionate scaling */
    width: "100%",
    height: "auto",
      
    /* Set up positioning */
    position: "relative"
}

export default Positions
